.switch input:checked + label { color: white; text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5); }

.switch input:focus + label { outline: none; }

.switch .switch-button { clear: both; position: absolute; top: -1px; left: 0; z-index: 1; width: 50%; height: 100%; background-color: #006632; transition: all .3s ease-out; color: #FFFFFF; border: 1px solid #006632; box-sizing: border-box; border-radius: 30px; box-shadow: 0px 8px 10px rgba(0, 102, 50, 0.15); }

.switch input:last-of-type:checked ~ .switch-button { left: 50%; }

#map { width: 100%; height: calc(100% - 10px); margin-top: 10px; }

.contact_block { background: #FFFFFF; box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1); padding: 24px 32px; width: 95%; margin: 0 auto; }

.contact_block p { font-weight: bold; font-size: 22px; line-height: 30px; color: #000000; border-bottom: 1px solid #C4C4C4; padding-bottom: 24px; }

.contact_block > * { display: flex; align-items: flex-start; margin-bottom: 20px; }

.contact_block > * img { margin-top: 5px; margin-right: 15px; }

.contacts_list { height: 100%; position: relative; overflow-y: scroll; }

.contact_block { margin-top: 10px; }

@media (max-height: 900px) { .contacts .top_block { padding-top: 120px; padding-bottom: 50px; }
  .contacts .map_main .row { height: 95%; } }

@media (max-width: 1549.9px) { .contact_block { font-size: 16px; }
  .switch label { font-size: 16px; } }

footer { padding-top: 95px; padding-bottom: 75px; background-color: #000000; }

.name_footer { text-align: center; }

.name_footer p { margin-top: 8px; font-size: 16px; line-height: 22px; color: #777676; margin-left: 25px; }

.footer_list { display: flex; justify-content: space-between; }

.footer_list li { margin-bottom: 16px; transition: all 0.2s; }

.footer_list li:hover { transform: scale(1.2); }

.footer_list li:hover a { color: #7DB928; }

.footer_list a { color: #FFFFFF; }

.footer_soc { position: static; text-align: center; }

@media (max-width: 1549.9px) { .footer_soc { text-align: end; }
  .footer_list { font-size: 16px; }
  .name_footer img { max-width: 180px; }
  .name_footer p { font-size: 15px; } }

@media (max-width: 1199.9px) { .footer_soc { margin-top: -15px; }
  .footer_soc a:not(:last-child) { margin-right: 0px; }
  .footer_soc a { display: inline-block; margin-top: 15px; }
  .footer_soc a:nth-child(2n - 1) { margin-right: 20px; } }

@media (max-width: 991.9px) { .footer_list { display: none; }
  .footer_soc { display: flex; justify-content: space-around; margin-top: 50px; }
  .footer_soc a { margin: 0 !important; }
  footer { padding-top: 70px; padding-bottom: 50px; }
  .name_footer img { max-width: none; } }

@media (max-width: 575.9px) { .name_footer img { max-width: 50vw; }
  .name_footer p { margin-left: 0; } }
